* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding: 20px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  font-size: 22px;
}

.country-list {
  padding: 0;
  font-size: 22px;
}

.list-item {
  display: flex;
  margin-bottom: 10px;
  align-items: center;
}

.list-item > img {
  margin-right: 10px;
}

.country-title {
  display: flex;
  align-items: center;
}

.country-name {
  font-size: 36px;
  margin-left: 10px;
}

.country-info {
  font-size: 22px;
}

.lang-item:not(:last-child)::after {
  content: ',';
}
